import '../components/component.css'
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';

function Register() {
  return (
    <Container>
      <div style={{marginTop: "0vh", marginBottom: "26vh"}} className="d-flex flex-row justify-content-center">
        <Form style={{background: "rgba(0, 0, 0, 0.2)", width: "30vw"}}
           className="col-3 p-5 pt-3 m-5 d-flex flex-column align-items-center">
           <Form.Group className="mb-1" controlId="formBasicEmail">
              <h1 className="text-center">Register</h1>
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label className="fw-bold">Name</Form.Label>
            <Form.Control style={{width: "20vw"}} type="text" placeholder="" />
          </Form.Group>
           <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label className="fw-bold">Email</Form.Label>
            <Form.Control style={{width: "20vw"}} type="email" placeholder="" />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label className="fw-bold">Password</Form.Label>
            <Form.Control style={{width: "20vw"}} type="password" placeholder="" />
          </Form.Group>
          <Button variant="primary" type="submit">
            Submit
          </Button>
        </Form>
      </div>
    </Container>
  );
}

export default Register;