import './component.css';
import Carousel from 'react-bootstrap/Carousel';
import { useNavigate } from 'react-router-dom'
import img1 from '../images/img1.jpg';
import img2 from '../images/img2.jpeg';
import img3 from '../images/img3.jpg';

function UncontrolledExample() {

  const navigate = useNavigate();

  function btnRegister() {
      navigate("/Signup");
  }

  return (
      <div className="opacity pl-5 pr-5 pt-4 pb-4 margin-0">
        <Carousel className="container">
          <Carousel.Item>
              <img
                style={{height: "60vh", opacity: "0.6"}}
                className="w-100 img-center"
                src={img1}
                alt="First slide"
              />
            <Carousel.Caption className="d-flex flex-column align-items-center" style={{top: "3vh"}}>
              <div className="shadow col w-100 p-5" style={{overflow: "hidden", height: "40vh", background: "rgba(0,0,0,0.2)", textAlign: "left" }}>
                <h1 style={{fontSize: "1.8rem"}} className="pb-3">Plan your Working Hours Flexibly</h1>
                <ul className="" style={{listStyle: "square", color: "black", fontSize: "1.6rem"}}>
                  <li className=""><p style={{color: "azure"}}>Improve work life balance</p></li>
                  <li><p style={{color: "azure"}}>Increase productivity</p></li>
                  <li><p style={{color: "azure"}}>Better health</p></li>
                </ul>
              </div>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
              <img
                style={{height: "60vh", opacity: "0.6"}}
                className="w-100"
                src={img2}
                alt="Second slide"
              />
              <Carousel.Caption className="d-flex flex-column align-items-center" style={{top: "3vh"}}>
              <div className="shadow col w-100 p-5" style={{overflow: "hidden", height: "40vh", background: "rgba(0,0,0,0.2)", textAlign: "left" }}>
              <h1 style={{fontSize: "1.8rem"}} className="pb-3">Work Schedule Planner</h1>
                <ul className="" style={{listStyle: "square", color: "black", fontSize: "1.6rem"}}>
                  <li className=""><p style={{color: "azure"}}>Managing Workload to take on tasks that we can handle effectively</p></li>
                  <li><p style={{color: "azure"}}>Scheduled tasks can improve efficiency if you consistently miss deadlines or lose leads</p></li>
                  <li><p style={{color: "azure"}}>Steady Progress Towards Your Goals</p></li>
                </ul>
              </div>
              </Carousel.Caption> 
          </Carousel.Item>
          <Carousel.Item>
              <img
                style={{height: "60vh", opacity: "0.8"}}
                className="w-100"
                src={img3}
                alt="First slide"
              />
              <Carousel.Caption className="d-flex flex-column align-items-center" style={{top: "3vh"}}>
              <div className="shadow col w-100 p-5 pt-4" style={{overflow: "hidden", height: "40vh", background: "rgba(0,0,0,0.2)", textAlign: "center" }}>
              <h1 style={{fontSize: "1.8rem"}} className="pb-3">"Improve your working performance more effectively and efficiently."</h1>
              <h2 style={{color: "azure"}} className="mt-5">Create a Schedule Plan now!</h2>
              <button id="btn1" type="button" onClick={() => btnRegister()} className="fw-bold fs-5 mt-2" style={{borderRadius: "30px", paddingLeft: "15px", 
                paddingRight: "15px", paddingTop: "10px", paddingBottom: "10px"}}>Get Started</button>
              </div>
              </Carousel.Caption> 
          </Carousel.Item>
        </Carousel>
      </div>
  );
}

export default UncontrolledExample;